* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  color: #fff;
  font-size: 20px;
  background-color: #222;
  font-family: "VT323", monospace;
}

button {
  background-color: maroon;
  color: #fff;
  font-family: "VT323", monospace;
  font-size: 20px;
  padding: 5px 10px;
  border: none;
  border-radius: 2px;
  cursor: pointer;
}

footer {
  text-align: center;
}

a {
  color: #81fe81;
}

.Text--Good {
  color: #00fc00;
}

.Text--Bad {
  color: #a10000;
}

.Text--DOB {
  color: #ffff00;
}

.Text--Family {
  color: #00c9c9;
}

.Text--Caption {
  font-size: 15px;
}
